import React, { useState } from "react";
import { Twirl as Hamburger } from "hamburger-react";
import { AnimatePresence, motion } from "framer-motion";
import "./Navbar.css";

import { Link, animateScroll } from "react-scroll";

import logob from "../../images/logob.png";

const Navbar = () => {
  const [notOnTop, setNotOnTop] = useState(false);
  const nvProps = () => {
    if (window.scrollY >= 50) {
      setNotOnTop(true);
    } else {
      setNotOnTop(false);
    }
  };
  window.addEventListener("scroll", nvProps);

  const [isOpen, setOpen] = useState(false);
  const toggle = () => {
    setOpen(!isOpen);
  };
  const menuVars = {
    initial: {
      scaleY: 0,
    },
    animate: {
      scaleY: 1,
      transition: {
        duration: 0.2,
      },
    },
    exit: {
      scaleY: 0,
      transition: {
        duration: 0.2,
      },
    },
  };

  return (
    <div className={notOnTop ? "NSuper NSuper-alt" : "NSuper"}>
      <div className="NContent">
        <div className={notOnTop ? "NLogo NLogo-alt" : "NLogo"}>
          <Link to="hero" spy={true} smooth={true} offset={0} duration={500}>
            <img src={logob} alt="Logo" />
          </Link>
        </div>
        <div className="NNavigation">
          <Link to="hero" spy={true} smooth={true} offset={0} duration={500}>
            <p>
              INIC<span>I</span>O
            </p>
          </Link>
          <Link
            to="servicios"
            spy={true}
            smooth={true}
            offset={-80}
            duration={500}
          >
            <p>
              SERVIC<span>I</span>OS
            </p>
          </Link>
          <Link
            to="clientes"
            spy={true}
            smooth={true}
            offset={-80}
            duration={500}
          >
            <p>
              CLIEN<span>T</span>ES
            </p>
          </Link>
          <Link
            to="equipo"
            spy={true}
            smooth={true}
            offset={-80}
            duration={500}
          >
            <p>
              EQU<span>I</span>PO
            </p>
          </Link>
          <Link
            to="partner"
            spy={true}
            smooth={true}
            offset={-80}
            duration={500}
          >
            <p>
              PART<span>N</span>ER
            </p>
          </Link>
          <Link
            to="contacto"
            spy={true}
            smooth={true}
            offset={-80}
            duration={500}
          >
            <p>
              CONTA<span>C</span>TO
            </p>
          </Link>
        </div>
        <div className="NHamburger">
          <Hamburger toggled={isOpen} toggle={setOpen} size={48} />
          {/* <button onClick={() => toggle()}>CLick Me</button> */}
          {/* toggle={setOpen} onClick={() => toggle()}*/}
        </div>
      </div>
      <AnimatePresence mode="wait" initial={false}>
        {isOpen && (
          <motion.div
            key="defkey"
            //variants={menuVars}
            initial={{ scaleY: 0, originY: 0 }}
            animate={{ scaleY: 1 }}
            exit={{ scaleY: 0 }}
            transition={{ duration: 0.3 }}
            className={notOnTop ? "NRNavigation NRN-alt" : "NRNavigation"}
          >
            <Link
              to="hero"
              spy={true}
              smooth={true}
              offset={0}
              duration={500}
              onClick={() => setOpen(false)}
            >
              <p className="NRNPTag">
                INIC<span>I</span>O
              </p>
            </Link>
            <Link
              to="servicios"
              spy={true}
              smooth={true}
              offset={-80}
              duration={500}
              onClick={() => setOpen(false)}
            >
              <p className="NRNPTag">
                SERVIC<span>I</span>OS
              </p>
            </Link>
            <Link
              to="clientes"
              spy={true}
              smooth={true}
              offset={-80}
              duration={500}
              onClick={() => setOpen(false)}
            >
              <p className="NRNPTag">
                CLIEN<span>T</span>ES
              </p>
            </Link>
            <Link
              to="equipo"
              spy={true}
              smooth={true}
              offset={-80}
              duration={500}
              onClick={() => setOpen(false)}
            >
              <p className="NRNPTag">
                EQU<span>I</span>PO
              </p>
            </Link>
            <Link
              to="partner"
              spy={true}
              smooth={true}
              offset={-80}
              duration={500}
              onClick={() => setOpen(false)}
            >
              <p className="NRNPTag">
                PART<span>N</span>ER
              </p>
            </Link>
            <Link
              to="contacto"
              spy={true}
              smooth={true}
              offset={-80}
              duration={500}
              onClick={() => setOpen(false)}
            >
              <p className="NRNPTag">
                CONTA<span>C</span>TO
              </p>
            </Link>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default Navbar;
